<template>
  <div class="container">
    <div class="row">
      <div v-for="type in productTypes" :key="type.id" class="card col-3">
        <router-link :to="'/productList/' + type.id">
          <img
            src="https://www.blms.io/wp-content/uploads/2017/11/logo%E9%80%8F%E6%98%8E-400x400.png"
            class="card-img-top"
            alt="..."
          />
        </router-link>
        <div class="card-body">
          <h6 class="card-title">{{ type.title }}</h6>
          <button @click="deleteType(type)">Delete</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ProductTypeCard",
  //What the component expects as parameters
  props: ["productTypes"],
  methods: {
    //prodcuts under this category will also be deleted
    async deleteType(type) {
      const res = await axios
        .delete("http://localhost:3000/types/" + type.id)
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>