<template>
  <div>
    <input
      type="text"
      placeholder="Add new product"
      v-model="newProductTitle"
      @keyup.enter="createProduct"
    />

    <ul class="list-unstyled">
      <li v-for="product in products" :key="product.id" class="media mt-4">
        <img
          src="https://www.blms.io/wp-content/uploads/2017/11/logo%E9%80%8F%E6%98%8E-400x400.png"
          class="mr-3"
          style="width:100px"
          alt="..."
        />
        <div class="media-body">
          <h5 class="mt-0 mb-1">{{ product.title }} - ${{ product.price }}</h5>Cras sit amet nibh libero, in gravida nulla. Nulla vel metus scelerisque ante sollicitudin. Cras purus odio, vestibulum in vulputate at, tempus viverra turpis. Fusce condimentum nunc ac nisi vulputate fringilla. Donec lacinia congue felis in faucibus.
        </div>
        <router-link :to="'/productEdit/' + product.id">
          <button>Edit</button>
        </router-link>
        <button @click="deleteProduct(product)">Delete</button>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ProductList",
  data: function() {
    return {
      products: [],
      index: 0,
      newProductTitle: ""
    };
  },
  mounted() {
    axios
      .get(
        "http://localhost:3000/types/" +
          this.$route.params.productTypeId +
          "/drinks"
      )
      .then(response => (this.products = response.data))
      .catch(error => {
        console.log(error);
      });
  },
  methods: {
    async deleteProduct(item) {
      const res = await axios
        .delete("http://localhost:3000/drinks/" + item.id)
        .catch(error => {
          console.log(error);
        });
    },
    async createProduct() {
      const res = await axios
        .post(
          "http://localhost:3000/types/" +
            this.$route.params.productTypeId +
            "/drinks",
          {
            title: this.newProductTitle
          }
        )
        .catch(error => {
          console.log(error);
        });
      this.newProductTitle = "";
    }
  }
};
</script>