<template>
  <div>
    {{ "product id: " + this.$route.params.productId }}
    <br />
		{{ "product title: " + this.product.title }}
    <br />
		{{ "type id: " + this.product.typeId }}
    <br />
		{{ "price: " + this.product.price }}
    <br />

		<input type=text placeholder="Enter new title" v-model="newTitle"/>
		<br />
		<input type=number placeholder="Enter new type id" v-model="newTypeId"/>
		<br />
		<input type=number placeholder="Enter new price" v-model="newPrice"/>
		<br/>
		<button v-on:click="editProduct">Submit</button>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ProductEdit",
  data: function() {
    return {
      product: {},
			newTitle: null,
			newTypeId: null,
			newPrice: null
    };
  },
  mounted() {
    axios
      .get("http://localhost:3000/drinks/" + this.$route.params.productId)
			.then(response => (
				this.product = response.data
			))
      .catch(error => {
        console.log(error);
      });
  },
  methods: {
    async editProduct() {
      const res = await axios
        .patch("http://localhost:3000/drinks/" + this.$route.params.productId, {
					title: this.newTitle,
					typeId: this.newTypeId,
					price: this.newPrice
        })
        .catch(error => {
          console.log(error);
        });
      this.newTitle = "";
    }
  }
};
</script>