<template>
  <div class="container-fluid">
    <input type="text" placeholder="Add new type" v-model="typeName" @keyup.enter="createType" />
    <ProductTypeCard :productTypes="types"></ProductTypeCard>
  </div>
</template>

<script>
import axios from "axios";
import ProductTypeCard from "../components/ProductTypeCard";

const baseURL = "http://localhost:3000";

export default {
  name: "Menu",
  data: function() {
    return {
      types: [],
      typeName: ""
    };
  },
  components: {
    ProductTypeCard
  },
  mounted() {
    axios
      .get(baseURL + "/types")
      .then(response => (this.types = response.data))
      .catch(error => {
        console.log(error);
      });
  },
  methods: {
    async createType() {
      const res = await axios
        .post(baseURL + "/types", {
          title: this.typeName
        })
        .catch(error => {
          console.log(error);
        });
      this.typeName = "";
    }
  }
};
</script>