<template>
  <div>
    <p>{{ title }}</p>
    <p>{{ address }}</p>
  </div>
</template>
 
<script>
import db from "../db.js";

export default {
  name: "Home",
  data: function() {
    return {
      title: null,
      address: null
    };
  },
  mounted() {
    db.collection("q1")
      .doc("item-types")
      .get()
      .then(snapshot => {
        this.title = snapshot.data().title;
        this.address = snapshot.data().address;
      });
  }
};
</script>