<template>
    <div class="base-timer">
        <h1>服務頁面</h1>
        <h1>{{hi('Petaaa')}}</h1>

    <!-- <h1>Testing Table </h1> -->
        <h2>Current Time :{{currentTime}}</h2>
        <!-- <h2>Start Time :{{tables[0].tableTimerStart}}</h2>
        <h2>time total:{{formattedTime(tables[0].timerSeconds)}}</h2>
        <button v-on:click="currentTime = new Date();
                timePassed=timeCalculate(tables[0].tableTimerStart,currentTime);
                startTimer();testingStatus=true">refresh</button>
        <button v-on:click="editJSONStartTime(currentTime -1, 0)">Start</button>
        <button v-on:click="stopTimer(0);
                table.tableRunning=false">Pause</button>
        <button v-on:click="resetTimer(index);">Reset</button> -->


        <div>
          <table>
            <tr>
              <th>桌台號碼</th>
              <th>開桌時間</th><!-- 後期刪除 -->
              <th>桌台計時</th>
              <th>商品費用</th>
              <th>桌台費用</th>
              <th>桌台狀態</th>
              <th>桌台控制</th>
            </tr>
            <tr v-for="(table,index) in tables">
              <td>{{table.num}}</td>
              <td>{{table.tableTimerStart}}</td><!--後期刪除-->
              <td>{{formattedTime(table.timerSeconds)}}</td>
              <td>{{table.productPrice}}$</td>
              <td>{{formattedCost(table.timerSeconds,table.tableRate)}}$</td>
              <td>{{formattedStatus(table.tableRunning)}}</td>
              <td><button v-on:click="currentTime=new Date();editJSONStartTime(currentTime-1,index)">Start</button>
                  <button v-on:click="stopTimer(index);table.tableRunning=false">Pause</button>
                  <button v-on:click="resetTimer(index);">Reset</button>
              </td>
            </tr>
          </table>

        

        
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";
import axios from "axios";
import db from "../db";
// const baseURL = "http://localhost:3000";
const baseURL = "http://192.168.1.113:4000";
export default {
  name: "Service",
  data: function() {
    return {
      data: {},
      tables: [],
      startTime: new Date(1588628805008),
      currentTime: new Date(),
      timePassed: 0,
      testTI: null,
      testingStatus: false,
      testingSave: 0
    };
  },
  computed: {
    // ...mapState(["tables"])
  },
  // firebase: {
  //   tables: db.ref("tables")
  // },
  mounted() {
    axios
      .get(baseURL + "/tables")
      .then(response => {
        this.tables = response.data;
        console.log(this.tables[0].timeSaved);
        for (var i = 0; i < this.tables.length; i++) {
          if (this.tables[i].tableRunning) {
            this.startTimer(i);
          }
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  methods: {
    hi(employeeName) {
      return "Welcome " + employeeName;
    },
    timeCalculate(timeA, timeB) {
      var timeDiff = parseInt(Math.abs(timeA - timeB) / 1000);
      return timeDiff;
    },
    getTimePassed() {
      return this.timePassed;
    },
    editJSONStartTime(tts, index) {
      const res = axios
        .patch(baseURL + "/tables/" + index, {
          tableTimerStart: tts,
          tableRunning: true
        })
        .catch(error => {
          console.log('editJSONStartTime"' + error);
        });
      // this.startTimer(index);
      console.log("tts:" + tts);
    },
    editJSONPause(index) {
      var tempSaved = this.tables[index].timeSaved;
      if (tempSaved != 0) {
        tempSaved = 0;
      }
      const res = axios
        .patch(baseURL + "/tables/" + index, {
          timerSeconds: this.tables[index].timerSeconds,
          tableTimerStart: this.currentTime - 1,
          tableRunning: false,
          timeSaved: tempSaved + this.tables[index].timerSeconds
        })
        .catch(error => {
          console.log(error);
        });
    },
    editJSONReset(index) {
      const res = axios
        .patch(baseURL + "/tables/" + index, {
          tableTimerStart: 0,
          timerSeconds: 0,
          tableRunning: false,
          timeSaved: 0
        })
        .catch(error => {
          console.log(error);
        });
    },
    startTimer(index) {
      var runs = this.testingStatus;

      /* this.timePassed = this.timeCalculate(
        this.tables[index].tableTimerStart,
        this.currentTime - 1
      ); */

      this.tables[index].timerSeconds =
        this.timeCalculate(
          this.tables[index].tableTimerStart,
          this.currentTime - 1
        ) + this.tables[index].timeSaved;

      if (!runs) {
        this.tables[index].timerInterval = setInterval(
          () => (this.tables[index].timerSeconds += 1),
          1000
        );

        // this.editJSONStartTime(this.currentTime - 1, index);
      }
    },
    stopTimer(index) {
      var runs = this.tables[index].tableRunning;
      if (runs) {
        //用來避免重複讀取造成時間倒退
        clearInterval(this.tables[index].timerInterval);
        this.tables[index].timerInterval = null;
        this.tables[index].tableRunning = false;
        this.editJSONPause(index);
      }
    },
    resetTimer(index) {
      this.tables[index].timerSeconds = 0;
      this.stopTimer(index);
      this.editJSONReset(index);
    },

    formattedTime(tts) {
      const timePassed = tts;

      const hours = Math.floor(timePassed / 3600);

      let minutes = Math.floor((timePassed % 3600) / 60);

      if (minutes < 10) {
        minutes = `0${minutes}`;
      }

      let seconds = timePassed % 60;

      if (seconds < 10) {
        seconds = `0${seconds}`;
      }
      // The output in MM:SS format
      return `${hours}:${minutes}:${seconds}`;
    },
    formattedStatus(stat) {
      var status = "N/A";
      const table = stat;
      if (table == true) {
        this.status = "桌台計時中~";
      } else {
        this.status = "桌台暫停中~";
      }
      return this.status;
    },
    formattedCost(tts, rate) {
      const timePassed = tts;
      const tableRate = rate;
      const table = Math.floor(timePassed / 3600 * tableRate);

      return table;
    }
  }
};
</script>


<style scoped>
table {
  width: 80%;
}
</style>
