<template>
  <div class="base-timer">
    <h1>請選擇桌號</h1>
    <select v-model="tables_index">
      <option v-for="(table,index) in tables" :value="index">{{table.num}}</option>
    </select>
    <h1>桌號: {{tables[tables_index].num}}</h1>
    <!-- <h1>開始時間: {{tables[tables_index].tableTimerStart}}</h1> -->
    <h1>您已經遊玩 : {{newTableTimerStart}}</h1>
    <!-- <h1>{{tables[tables_index].timerSeconds}}</h1> -->
    <!-- <h1>您已經遊玩 : {{formattedTime(timeCalculate(tables[tables_index].tableTimerStart, currentTime)+tables[tables_index].timeSaved)}}</h1> -->
    <h1>桌台費: {{formattedCost(tables[tables_index].timerSeconds,tables[tables_index].tableRate)}}$</h1>
    <h1>桌台狀態: {{formattedStatus(tables[tables_index].tableRunning)}}</h1>
  </div>
</template>

<script>
import axios from "axios";
//const baseURL = "http://localhost:3000";
const baseURL = "http://192.168.1.113:4000";
export default {
  name: "Bill",
  data: function() {
    return {
      data: {},
      timerInterval: null,
      tables_index: 0,
      tables: [],
      tagetTable: [],
      currentTime: new Date()
    };
  },
  methods: {
    hi(employeeName) {
      return "Welcome " + employeeName;
    },
    timeCalculate(timeA, timeB) {
      var timeDiff = parseInt(Math.abs(timeA - timeB) / 1000);
      return timeDiff;
    },
    startTimer(index) {
      var runs = this.testingStatus;

      /* this.timePassed = this.timeCalculate(
        this.tables[index].tableTimerStart,
        this.currentTime - 1
      ); */

      this.tables[index].timerSeconds =
        this.timeCalculate(
          this.tables[index].tableTimerStart,
          this.currentTime - 1
        ) + this.tables[index].timeSaved;

      if (!runs) {
        this.tables[index].timerInterval = setInterval(
          () => (this.tables[index].timerSeconds += 1),
          1000
        );

        // this.editJSONStartTime(this.currentTime - 1, index);
      }
    },
    formattedTime(tts) {
      const timePassed = tts;

      const hours = Math.floor(timePassed / 3600);

      let minutes = Math.floor((timePassed % 3600) / 60);

      if (minutes < 10) {
        minutes = `0${minutes}`;
      }

      let seconds = timePassed % 60;

      if (seconds < 10) {
        seconds = `0${seconds}`;
      }
      // The output in MM:SS format
      return `${hours}:${minutes}:${seconds}`;
    },
    formattedStatus(stat) {
      const status = "N/A";
      const table = stat;
      if (table == true) {
        this.status = "桌台計時中~";
      } else {
        this.status = "桌台暫停中~";
      }
      return this.status;
    },
    formattedCost(tts, rate) {
      const timePassed = tts;
      const tableRate = rate;
      const table = Math.floor(timePassed / 3600 * tableRate);

      return table;
    }
  },

  mounted() {
    axios
      .get(baseURL + "/tables")
      .then(response => {
        this.tables = response.data;
        console.log(this.tables[0].timeSaved);
        for (var i = 0; i < this.tables.length; i++) {
          if (this.tables[i].tableRunning) {
            this.startTimer(i);
          }
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  computed: {
    newTableTimerStart() {
      var vm = this;
      if (vm.tables[vm.tables_index].tableRunning) {
        return vm.formattedTime(vm.tables[vm.tables_index].timerSeconds);
      } else {
        return vm.formattedTime(vm.tables[vm.tables_index].timeSaved);
      }
    }
    // ...mapState(["tables"])
  }
};
</script>