import Vue from 'vue';
import App from './App.vue';
import router from "./router";
import store from "./store.js";

import "bootstrap";
import "bootstrap/dist/css/bootstrap.css";

import { rtdbPlugin } from 'vuefire';

Vue.use(rtdbPlugin);

Vue.config.productionTip = false;

new Vue({
  store,
  router,
  render: h => h(App),
}).$mount('#app');
