<template>
<div id="app">
  <body>
    <h1 style="  text-align: center;">{{$store.state.playerCount}} 人模式</h1>
    <table>
      <button v-on:click="$store.state.playerCount = 2">2 Players</button>
      <button v-on:click="$store.state.playerCount = 3">3 Players</button>
      <button v-on:click="$store.state.playerCount = 4">4 Players</button>

      <tr>
        <th />
        <input type="text" id="n1" value="P1" size="5" />
        <th />
        <button v-on:click="$store.state.p1Score --">-</button>
        <th />
        <h1 style="  text-align: center;">{{$store.state.p1Score}}</h1>
        <th />
        <button v-on:click="$store.state.p1Score ++">+</button>
      </tr>

      <tr>
        <th />
        <input type="text" id="T1" value="P2" size="5" />
        <th />
        <button v-on:click="$store.state.p2Score --">-</button>
        <th />
        <h1 style="  text-align: center;">{{$store.state.p2Score}}</h1>
        <th />
        <button v-on:click="$store.state.p2Score ++">+</button>
      </tr>

      <tr id="n3" v-if="$store.state.playerCount >= 3">
        <th />
        <input type="text" value="P3" size="5" />
        <th />
        <button v-on:click="$store.state.p3Score--">-</button>
        <th />
        <h1 style="  text-align: center;">{{$store.state.p3Score}}</h1>
        <th />
        <button v-on:click="$store.state.p3Score ++">+</button>
      </tr>

      <tr id="n4" v-if="$store.state.playerCount >= 4">
        <th />
        <input type="text" value="P4" size="5" />
        <th />
        <button v-on:click="$store.state.p4Score--">-</button>
        <th />
        <h1 style="  text-align: center;">{{$store.state.p4Score}}</h1>
        <th />
        <button v-on:click="$store.state.p4Score ++">+</button>
      </tr>
    </table>
  </body>
</div>
</template>

<script>

export default {
  name: "ScoreKeeper",
  data: function() {
    return {
      p1: 0,
      p2: 0,
      p3: 0,
      p4: 0
    };
	},
  methods: {
    // p1add: function(){
    //  return p1++;
    // }
  }
};
</script>
<style scoped>
input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 50px;
  text-align: center;
}
</style>